.container-group32 {
    margin-top: 92px;
    margin-left: 317px;
    margin-right: 317.95px;
    text-align: center;
}

.card32 {
    border-radius: 75px;
    background: #3664D8;
    display: grid;
    grid-template-rows: 37.6px 37.6px;
    grid-row-gap: 4.8px;
    grid-template-columns: 80px 465.33px 114.67px;
}

.cards32 {
    display: grid;
    grid-template-rows: repeat(2, 1fr);
    grid-row-gap: 25px;
    margin: auto;
    margin-top: 53px;
    width: 660px;
}

.card32>img {
    margin-left: 15px;
    margin-top: 15px;
}

.card32text {
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.25px;
    color: #FFFFFF;
    margin-top: 20px;
    margin-bottom: 0;
}

.card32h3 {
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 0.25px;
    color: #FFFFFF;
    opacity: 0.5;
    margin-top: 0;
}

.center-row {
    grid-row-start: 1;
    grid-row-end: 2;
    grid-column-start: 2;
    text-align: left;
}

.right-row {
    grid-row-start: 1;
    grid-row-end: 2;
    grid-column-start: 3;
    grid-column-end: 3;
    margin-right: 36px;
}

.time {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
    text-align: right;
    letter-spacing: 0.25px;
    color: #FFFFFF;
    opacity: 0.5;
    margin-top: 16px;
    margin-bottom: 0;
}

.stars {
    display: flex;
}

.star {
    margin-top: 0;
    width: 16px;
    margin-right: 2.67px;
}

.h1bottom {
    text-transform: capitalize;
    font-style: normal;
    font-weight: 200;
    font-size: 26.3476px;
    line-height: 110%;
    color: #212353;
    white-space: nowrap;
    margin-top: 43px;
}

.textbottom {
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 180%;
    letter-spacing: 0.01em;
    color: #212353;
    margin-bottom: 34px;
    white-space: nowrap;
}